/*
 * Side navigation
 *
 * Scrollspy and affixed enhanced navigation to highlight sections and secondary
 * sections of docs content.
 */
/* By default it's not affixed in mobile views, so undo that */
.bs-sidebar.affix {
  position: static; }

/* First level of nav */
.bs-sidenav {
  margin-top: 30px;
  margin-bottom: 30px;
  margin-right: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  text-shadow: 0 1px 0 #fff;
  background-color: #f7f5fa;
  border-radius: 5px; }

/* All levels of nav */
.bs-sidebar .nav > li > a {
  display: block;
  color: #716b7a;
  padding: 5px 20px; }

.bs-sidebar .nav > li > a:hover,
.bs-sidebar .nav > li > a:focus {
  text-decoration: none;
  background-color: #e5e3e9;
  border-right: 1px solid #dbd8e0; }

.bs-sidebar .nav > .active > a,
.bs-sidebar .nav > .active:hover > a,
.bs-sidebar .nav > .active:focus > a {
  font-weight: bold;
  color: #e5807b;
  background-color: #e5e3e9;
  border-right: 1px solid #e5807b; }

/* Nav: second level (shown on .active) */
.bs-sidebar .nav .nav {
  display: none;
  /* Hide by default, but at >768px, show it */
  margin-bottom: 8px; }

.bs-sidebar .nav .nav > li > a {
  padding-top: 3px;
  padding-bottom: 3px;
  padding-left: 30px;
  font-size: 90%; }

.bs-sidebar .nav > li > a {
  display: block;
  color: #716b7a;
  padding: 5px 20px; }

/* Front Page img grid
*  Note!
*  To display grid images, remove opacity from:
*  .panels-flexible-region .pane-content img
*  and remove background-color from:
*  .panels-flexible-region .pane-title
*/
.img-grid .node-teaser h2 {
  display: none; }

.img-grid div.pane-node {
  position: relative; }

.img-grid h2 a {
  position: absolute;
  left: 10px;
  top: 10px;
  z-index: 1;
  text-shadow: 2px 2px #333;
  color: white;
  /* transition: color 0.4s ease; */ }

.color-grid-item h2 a {
  position: absolute;
  font-size: 0.75em;
  left: 10px;
  top: 10px;
  z-index: 1;
  text-shadow: 2px 2px #333;
  color: white;
  /* transition: color 0.4s ease; */ }

.img-grid .pane-title a:hover {
  color: #fa7f00;
  /*
          $orange: #e87511; //Princeton orange
          $orange-light: #fff7f3;
          $orange-dark: #7F3D00;*/
  text-decoration: none; }

div.img-grid img {
  cursor: pointer; }

.col-sm-6.img-grid img {
  width: 273px; }

.col-sm-12.img-grid img {
  width: 455px; }

.img-grid .field-item {
  background-color: black;
  border-radius: 6px; }

/* Mudd and Firestone img grids */
.grid-menu-with-sidebar .img-grid .pane-content img {
  position: absolute;
  left: 0px;
  top: 0px;
  border-radius: 6px;
  /*z-index: -1;
    opacity: 0;*/ }

.grid-menu-with-sidebar .img-grid .pane-title a {
  color: white;
  display: block;
  font-size: 1em;
  /* transition: color 0.4s ease; */ }

.grid-menu-with-sidebar .img-grid .pane-title a:hover {
  color: ivory;
  text-decoration: none; }

.grid-menu-with-sidebar .img-thumbnail {
  margin-top: 20px;
  margin-bottom: 10px; }

.color-grid-item, .color-grid-item-small {
  background-color: black;
  border-radius: 6px; }

.darken {
  opacity: .8;
  filter: alpha(opacity=80);
  filter: opacity(0.8);
  -webkit-filter: opacity(0.8);
  -moz-filter: opacity(0.8);
  -ms-filter: opacity(0.8);
  -o-filter: opacity(0.8);
  border: 0px solid #ffc04c; }

.darken-hover {
  opacity: 1;
  filter: alpha(opacity=100);
  filter: opacity(1);
  -webkit-filter: opacity(1);
  -moz-filter: opacity(1);
  -ms-filter: opacity(1);
  -o-filter: opacity(1);
  border: 0px solid #ffc04c; }

.darken-hover h2 a {
  color: #fa7f00;
  /*
    $orange: #e87511; //Princeton orange
    $orange-light: #fff7f3;
    $orange-dark: #7F3D00;*/
  text-decoration: none; }

